<template>
  <div class="common-layout">
    <el-container style="height: 100vh;">
      <el-header>
        <el-row>
          <el-text size="large">欢迎,{{ major_name }} 的 {{ user_name }}</el-text>
        </el-row>
        <el-row>
          <el-button @click="退出系统对话()" type="primary">退出系统</el-button>
        </el-row>
      </el-header>
      <el-container height=100%>
        <el-aside width="150px">
          <el-menu default-active="0" :router="false" @select="handleSelect">
            <el-menu-item index="0">
              <el-icon><icon-menu /></el-icon>
              <span>所有课程</span>
            </el-menu-item>
            <el-menu-item index="1">
              <el-icon>
                <IconSelectAll />
              </el-icon>
              <span>课程管理</span>
            </el-menu-item>
            <el-menu-item index="2">
              <el-icon>
                <IconSearch />
              </el-icon>
              <span>成绩查询</span>
            </el-menu-item>
            <el-menu-item index="3">
              <el-icon>
                <IconCalendar />
              </el-icon>
              <span>课程表</span>
            </el-menu-item>
            <el-menu-item index="4">
              <el-icon><icon-info /></el-icon>
              <span>个人信息</span>
            </el-menu-item>
          </el-menu>
        </el-aside>
        <el-main>
          <RouterView></RouterView>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

  
<script>
/* INTRO 2023-12-11: 引入 student 模块的 api */
/* NOTE 2023-12-08: @ 是 /src 的化名 */
import student_api from '@/api/student/student-api'
import student_function from '@/api/student/student-function'
export default {

  data() {
    return {
      msg: "student home page",
      user_id: "",
      user_name: "",
      major_code: "",
      major_name: "",

    }
  },

  mounted() {
    this.user_id = this.$route.query.user_id /* INTRO 2023-12-11: 获取其他页面传递过来的数据 */
    student_api.获取个人信息(
      { user_id: this.user_id }
    ).then(resp => {
      if(resp.status_code == 200){
        this.user_name = resp.data.user_name,
        this.major_code = resp.data.major_code,
        this.major_name = resp.data.major_name
        let data = { user_id: this.user_id, major_code: this.major_code }
        this.$router.push({
        path: '/student/home/mainPage',
        query: data
      })
      }
      else{
        student_function.Alert_Error(this,resp.status_text)
      }
    })
  },

  methods: {
    click_btn() {

      /* 调用 student 模块的 api */
      student_api.hello_world()
        .then(resp => {
          console.log(resp)
          this.msg = resp.data
        })
        .catch(ree => {
          console.error(err)
        })

      /* 调用 student 模块的 api */
      student_api.ice_bee()
        .then(resp => {
          this.$message.normal(resp.data)
        })
        .catch(ree => {
          console.error(err)
        })
    },
    handleSelect(index) {
      let data = { user_id: this.user_id, major_code: this.major_code }
      if (index == '0') {
        this.$router.push({
          path: '/student/home/mainPage',
          query: data
        })
      }
      if (index == '1') {
        this.$router.push({
          path: '/student/home/selectCoursePage',
          query: data
        })
      }
      if (index == '2') {
        this.$router.push({
          path: '/student/home/courseManagePage',
          query: data
        })
      }
      if (index == '3') {
        this.$router.push({
          path: '/student/home/courseTablePage',
          query: data
        })
      }
      if (index == '4') {
        this.$router.push({
          path: '/student/home/selfInfoPage',
          query: data
        })
      }
    },
    退出系统对话() {
      /**简易的对话框
       * 
       * 传入参数已经用中文说明
       */
      this.$confirm("确认退出系统?", "退出", {
        confirmButtonText: "确认",
        cancelButtonText: "取消",
        type: 'warning'
      }).then(() => {
        this.$message.success("再见！")
      this.$router.replace({
        path:'/'
        });
      }).catch(() => {

      });
    },
  }
}
</script>

<style scoped> /* NOTE 2023-12-08: style 添加 scoped 属性以将 CSS 仅限于此组件 */

 .el-header {
   position: relative;
   background-color: var(--el-color-primary-light-5);
   color: var(--el-text-color-primary);
 }

 .el-aside {
   color: var(--el-text-color-primary);
   background: var(--el-color-primary-light-7);
 }

 .el-container{
  height:100%
 }
 .el-main {
   background-color: var(--el-color-primary-light-8);
   color: var(--el-text-color-primary);
 }
</style>
