/* INTRO 2023-12-11: 引入 http_req 用于创建请求 */
import http_req from "@/utils/http-req"

export default {

  /* INTRO 2023-12-11: 定义与后端对应的api */
  hello_world() {
    return http_req({
      url: "/hello-world",
      method: "get"
    })
  },

  ice_bee() {
    return http_req({
      url: "/icebee",
      method: "get"
    })
  },
  获取个人信息(student_indentity_DTO){
    return http_req({
      url:"/student/get_self_info",
      method:"post",
      data:student_indentity_DTO
    })
  },

  获取所有课程(){
    return http_req({
      url:"/student/get_all_courses_info",
      method:"post",
    })
  },
  获取可选课程(student_major_DTO){
    return http_req({
      url:"/student/get_operational_courses",
      method:"post",
      data:student_major_DTO
    })
  },
  获取已选课程(student_indentity_DTO){
    return http_req({
        url:"/student/get_selected_course_info",
        method:"post",
        data:student_indentity_DTO
    })
  },
  获取课程表(student_indentity_DTO){
    return http_req({
      url:"/student/get_timetable",
      method:"post",
      data:student_indentity_DTO
    })
  },


  选课(CourseIdentityDTO){
    return http_req({
      url:"/student/select_course",
      method:"post",
      data:CourseIdentityDTO
    })
  },
  退课(CourseIdentityDTO){
    return http_req({
      url:"/student/quit_course",
      method:"post",
      data:CourseIdentityDTO
    })
  }

}
