import { ElMessage } from 'element-plus'
export default {
    身份校验检查(that,user_id) {
        if (user_id === "undefined" || typeof user_id !== 'string' || user_id === null) {
            // this.对话('滚', '身份验证错误', '6', '6', '6', '6',that);
            that.$router.replace({
                path: '/',
            });
        }
    },
    Alert_Error(that,msg) {
        /**弹窗警告
         * 类型：错误
         * 
         * 传入参数：
         * 要输出的信息:msg
         */
        that.$message.error(msg)
    },
    Alert_Success(that,msg) {
        /**弹窗
         * 类型：正常
         * 
         * 传入参数：
         * 要输出的信息:msg
         */
        that.$message.success(msg)
    },
    对话(内容, 标题, 取消文本, 确认文本, 取消弹出文本, 确认弹出文本,that) {
        /**简易的对话框
         * 
         * 传入参数已经用中文说明
         */
        that.$confirm(内容, 标题, {
            confirmButtonText: 确认文本,
            cancelButtonText: 取消文本,
            type: 'warning'
        }).then(() => {
            that.$message.info({
                type: 'success',
                message: 成功
            });
        }).catch(() => {
            that.$message.info({
                type: 'info',
                message: 取消弹出文本
            });
        });
    },
}
